.App {
  text-align: center;
}

.App-header {
  background-color: #ffffff;
  min-height: 5vh;
  align-items: center;
  font-size: calc(20px);
  color: rgb(0, 0, 0);
}

.ConnectFour {
  margin: 20px;
  display: flex;
  justify-content: center;
}

.Board {
  display: flex;
}

.Square {
  padding: 5px;
  margin: 2px;
  min-width: 40px;
  min-height: 40px;
  border: 2px solid #4b13ce;
}

.Square div {
  position: relative;
  min-width: 40px;
  min-height: 40px;
  border-radius: 20px;
  top: -400px;
}

.Red {
  background-color: #FF456C;
  transform: translateY(400px);
}

.Yellow {
  background-color: #ffea2c;
  transform: translateY(400px);
}

.victoryMessage {
  margin: 20px;
  font-size: 0pt;
  font: 10pt;
  opacity: 0;
}

.appear {
  opacity: 1;
  font-size: 10pt;
}
